import IReportRow from "./report-row";

const REPORT_URL = "https://bond-finder-lab.github.io/backend/report-v1.json?_=" + Date.now();

interface IReport {
    date: string;
    rows: ReadonlyArray<IReportRow>;
}

function convert(source: any): IReport {
    const cols = source.cols as string[];
    const rows = source.rows as any[];

    const indexOf: { [jsonKey: string]: number } = {};
    for (const name of cols) {
        indexOf[name] = cols.indexOf(name);
    }

    return {
        date: String(source.date),
        rows: rows.map((r) => ({
            isin: String(r[indexOf.isin]),
            name: String(r[indexOf.name]),
            sector: Number(r[indexOf.sector]),
            listLevel: Number(r[indexOf.list_level]),
            totalTradesPerc: Number(r[indexOf.total_trades_perc]),
            maturityDate: String(r[indexOf.maturity_date]),
            minDaysBetweenAmort: Number(r[indexOf.min_days_between_amort]),
            couponMonths: r[indexOf.coupon_months] as number[],
            couponValues: r[indexOf.coupon_values] as number[],
            closeYield: Number(r[indexOf.close_yield]),
            inABI: Boolean(r[indexOf.in_abi]),
            fits: false
        }))
    };
}

export default (callback: (report: IReport) => void) => {
    const xhr = new XMLHttpRequest();
    xhr.open("GET", REPORT_URL, true);
    xhr.timeout = 10 * 1000;

    function handleError() {
        alert("Не удалось загрузить данные. Мож инет отвалился?..");
        location.reload();
    }

    xhr.onreadystatechange = function() {
      if (this.readyState === 4) {
        if (this.status === 200) {
            try {
                callback(convert(JSON.parse(this.responseText)));
            } catch {
                handleError();
            }
        } else {
            handleError();
        }
      }
    };

    xhr.send();
};
