const STORAGE_KEY = "bond-finder-settings-v1";

export interface ISettings {
    month: string;
    year: string;
    sector1: boolean;
    sector2: boolean;
    sector3: boolean;
    maxListLevel: string;
    maxPerYear: string;
    requiredCouponMonth: string;
    couponType: string;
    maxAmort: string;
    minTotalTradesPerc: string;
}

const DEFAULT_SETTINGS: ISettings = {
    month: "01",
    year: String(new Date().getFullYear() + 4),
    sector1: true,
    sector2: true,
    sector3: true,
    maxListLevel: "2",
    maxPerYear: "4",
    requiredCouponMonth: "0",
    couponType: "0",
    maxAmort: "90",
    minTotalTradesPerc: "50"
};

export function load(): ISettings {
    const result = { ...DEFAULT_SETTINGS };
    let storedSettings: any;

    try {
        storedSettings = JSON.parse(localStorage.getItem(STORAGE_KEY));
    } catch {
        // mute
    }

    if (storedSettings && typeof storedSettings === "object") {
        for (const key of Object.keys(DEFAULT_SETTINGS)) {
            if (key in storedSettings) {
                result[key] = storedSettings[key];
            }
        }
    }

    return result;
}

export function save(settings: ISettings) {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(settings));
}
